<template>
  <div id="map">
    <el-button-group>
      <el-button type="primary" size="mini" @click="point">{{
        !toolRunning ? "开启画点" : "关闭画点"
      }}</el-button>
      <el-button type="primary" size="mini" @click="clear">清除</el-button>
      <el-button type="primary" size="mini" @click="exportFile"
        >文件导出</el-button
      >
      <el-upload
        class="upload"
        action="/api/uploadFile"
        accept="json"
        :show-file-list="false"
        :file-list="fileList"
        :on-success="onSucess"
        :on-error="onError"
      >
        <el-button size="mini" type="primary">点击上传</el-button>
      </el-upload>
    </el-button-group>
  </div>
</template>

<script>
import "ol/ol.css";
import Map from "ol/Map";
import View from "ol/View";
import Feature from "ol/Feature";
import { Draw, Modify, Snap } from "ol/interaction";
import { Point } from "ol/geom";
import { Vector as VectorSource, TileImage, XYZ } from "ol/source";
import { Tile as TileLayer, Vector as VectorLayer } from "ol/layer";
import { Circle as CircleStyle, Icon, Fill, Stroke, Style } from "ol/style";
import axios from "axios";
export default {
  name: "Home",
  data() {
    return {
      map: null,
      toolRunning: false,
      draw: null,
      sanp: null,
      editSource: null,
      fileList: []
    };
  },
  components: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.mapInit();
    },
    mapInit() {
      const picLayer = this.createTDT("baseLayerImg", "img_c");
      const textLayer = this.createTDT("baseLayerCia", "cia_c");
      const vector = this.editLayerInit();
      this.map = new Map({
        layers: [picLayer, textLayer, vector],
        target: "map",
        view: new View({
          projection: "EPSG:4326",
          center: [103, 36],
          zoom: 7,
          maxZoom: 18
        })
      });
      this.modifyInit();
    },
    editLayerInit() {
      this.editSource = new VectorSource({ wrapX: false });
      const vectorLayer = new VectorLayer({
        source: this.editSource,
        style: new Style({
          fill: new Fill({
            color: "rgba(255, 255, 255, 0.2)"
          }),
          stroke: new Stroke({
            color: "#ffcc33",
            width: 2
          }),
          image: new CircleStyle({
            radius: 7,
            fill: new Fill({
              color: "#ffcc33"
            })
          })
        })
      });
      return vectorLayer;
    },
    modifyInit() {
      const modify = new Modify({ source: this.editSource });
      this.map.addInteraction(modify);
    },
    addFeatureFromFile(arr) {
      arr.forEach(coordinate => {
        this.editSource.addFeature(
          new Feature({
            geometry: new Point(coordinate)
          })
        );
      });
    },
    getFeatureCoordinate() {
      const arr = [];
      this.editSource.getFeatures().forEach(feature => {
        arr.push(feature.getGeometry().flatCoordinates);
      });
      return arr;
    },
    createTDT(id, type) {
      const KEY = "af76e2d81e4d0b03e7c63a222d96a6bb";
      const layer = new TileLayer({
        source: new XYZ({
          url:
            "http://t0.tianditu.com/DataServer?T=" +
            type +
            "&x={x}&y={y}&l={z}&tk=" +
            KEY,
          projection: "EPSG:4326"
        })
      });
      layer.id = id;
      return layer;
    },
    point() {
      if (this.toolRunning) {
        // 关闭选点
        this.map.removeInteraction(this.draw);
        this.map.removeInteraction(this.snap);
        this.draw = null;
        this.snap = null;
      } else {
        // 开启选点
        this.draw = new Draw({
          source: this.editSource,
          type: "Point"
        });
        this.map.addInteraction(this.draw);
        this.snap = new Snap({ source: this.editSource });
        this.map.addInteraction(this.snap);
      }
      this.toolRunning = !this.toolRunning;
    },
    clear() {
      this.editSource.clear();
    },
    exportFile() {
      this.getFeatureCoordinate();
      axios
        .post(
          "/api/writeFile",
          {
            pointList: this.getFeatureCoordinate()
          },
          {
            headers: {
              "Content-Type": "application/json"
            }
          }
        )
        .then(res => {
          console.log(res);
          this.$message.success("导出成功");
          window.open('http://localhost:3000/api/exportFile');
        })
        .catch(error => {
          console.log(error);
        });
    },
    onSucess(response, file, fileList) {
      console.log(response);
      this.$message.success("解析成功");
      this.addFeatureFromFile(response.data.pointList);
    },
    onError(error, file, fileList) {
      console.log(error);
      this.$message.error("解析失败，请重试");
    }
  }
};
</script>
<style lang="scss" scoped>
#map {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative;
  .el-button-group {
    width: 300px;
    position: absolute;
    z-index: 5;
    top: 20px;
    right: 50px;
  }
}
</style>
