import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./plugins/element.js";
import { Button, Upload, ButtonGroup, Message } from "element-ui";
Vue.config.productionTip = false;

Vue.use(Button);
Vue.use(Upload);
Vue.use(ButtonGroup);
Vue.use(Message);

Vue.prototype.$message = Message;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
